/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { Link } from 'gatsby';
import { Card, CardActionArea, CardMedia, CardContent, Typography } from '@material-ui/core';
import { useMemo } from 'react';

interface Props {
  thumbnail: string;
  title: string;
  content: string;
  path: string;
  date?: string;
  categories?: string[];
  onClick?: (path: string) => void;
}
const SimplePostCard = ({ thumbnail, title, content, path, date = '', categories = [], onClick }: Props) => {
  const category = useMemo(() => categories.join('/'), [categories]);
  return (
    <Card onClick={() => onClick?.(path)}>
      <CardActionArea>
        <Link
          to={path}
          css={css`
            color: inherit;
            text-decoration: none;
          `}
        >
          <CardMedia component="img" image={thumbnail} title={title} alt={title} height="280" />
          <CardContent>
            <Typography gutterBottom color="textSecondary" variant="body2" component="p">
              {date} {category}
            </Typography>
            <Typography gutterBottom variant="h5" component="h2">
              {title}
            </Typography>
            <Typography color="textSecondary" component="p" dangerouslySetInnerHTML={{ __html: content }} />
          </CardContent>
        </Link>
      </CardActionArea>
    </Card>
  );
};

export default SimplePostCard;
