/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import SimplePostCard from 'src/components/Cards/SimplePostCard';
import { Post } from 'models/post';
import { useCallback } from 'react';
import { linkEvent } from 'utils/loggers';

interface Props {
  posts?: Post[];
}

const Posts = ({ posts = [] }: Props) => {
  const handleClick = useCallback((url: string) => {
    linkEvent(url, '메인 아티클 링크 클릭');
  }, []);

  return (
    <ul
      css={css`
        margin: 0;
        padding: 0;
      `}
    >
      {posts.map(post => {
        const { frontmatter, fields, excerpt } = post;
        const title = frontmatter.title ?? fields.slug;
        const path = fields.path ?? fields.slug;
        const content = frontmatter.description ?? excerpt ?? '';
        const thumbnail = frontmatter.thumbnail.childImageSharp.fluid?.src ?? '';
        const date = frontmatter.date;
        const categories = frontmatter.categories;
        console.log(fields);

        return (
          <li
            key={fields?.slug}
            css={css`
              list-style: none;
              margin: 0 0 1rem 0;
              padding: 0;
            `}
          >
            <SimplePostCard
              key={fields?.slug}
              thumbnail={thumbnail}
              title={title}
              path={path}
              content={content}
              date={date}
              categories={categories}
              onClick={handleClick}
            />
          </li>
        );
      })}
    </ul>
  );
};

export default Posts;
